@import 'normalize';
@import 'variables';
@import 'utils';
@import 'grid';


@for $i from 1 through $gridcolumns {
    .col-#{$i} {
        box-sizing: border-box;
        width: percentage($i/$gridcolumns); //width: 3/4 * 100
        padding-left: $gridgutter / 2;
        padding-right: $gridgutter / 2;
    }
    .offset#{$i} {
        margin-left: percentage($i/$gridcolumns);
    }
}

.col{
    box-sizing: border-box;
    padding-left: $gridgutter / 2;
    padding-right: $gridgutter / 2;
    flex: 1; //proporção; 1 = todas as cols têm a mesma proporção
}